<template>
  <div class="home">
    <music></music>
    <!-- 导航栏 -->
    <el-row class="menu">
      <div class="menu_cont">
        <div class="menu_cont_item">
          <div class="menu_cont_item_left .left">
            <i class="el-icon-s-home"></i>闪烁之狐
          </div>
          <div class="menu_cont_item_right">
            <el-menu
              :default-active="activeIndex"
              mode="horizontal"
              @select="handleSelect"
            >
              <el-menu-item index="1">
                <i class="el-icon-s-home"></i>首页</el-menu-item
              >

              <el-menu-item index="2">
                <i class="el-icon-menu"></i>我的工作台</el-menu-item
              >
              <el-menu-item index="3">
                <i class="el-icon-user-solid"></i>关于我</el-menu-item
              >
            </el-menu>
          </div>
        </div>
      </div>
    </el-row>

    <!-- banner图 -->
    <el-carousel height="100vh" :interval="5000" class="banner">
      <el-carousel-item v-for="(item, index) in imgData" :key="index">
        <img :src="item.url" alt="" />
      </el-carousel-item>
    </el-carousel>
    <!-- 内容区 -->
    <main class="main">
      <el-row type="flex" justify="center">
        <el-col :span="20" class="main_content">
          <el-card>
            <el-row type="flex" justify="center">
              <el-col :span="20">
                <div class="dream">
                  <div class="title center-align">
                    <i class="el-icon-s-opportunity"></i>&nbsp;&nbsp;我的梦想
                  </div>

                  <div class="text">
                    不是每个人都应该像我这样去建造一座水晶大教堂，但是每个人都应该拥有自己的梦想，设计自己的梦想，追求自己的梦想，实现自己的梦想。梦想是生命的灵魂，是心灵的灯塔，是引导人走向成功的信仰。有了崇高的梦想，只要矢志不渝地追求，梦想就会成为现实，奋斗就会变成壮举，生命就会创造奇迹。——罗伯·舒乐
                  </div>
                </div>

                <div class="video">
                  <div class="title center-align">
                    <i class="el-icon-video-camera-solid"></i>&nbsp;&nbsp;
                    精彩视频
                  </div>
                  <div id="dplayer"></div>
                </div>

                <div class="article">
                  <div class="title center-align">
                    <i class="el-icon-date"></i>&nbsp;&nbsp;
                    推荐文章
                  </div>
                  <div id="dplayer"></div>
                </div>
              </el-col>
            </el-row>
          </el-card>
        </el-col>
      </el-row>
    </main>
  </div>
</template>

<script>
import DPlayer from "dplayer";
import music from "../music/music.vue";
export default {
  name: "home",
    components: { music },
  data() {
    return {
      activeIndex: "1",
      imgData: [
        { title: "1", url: "https://t1.picb.cc/uploads/2021/04/04/ZBnLWX.jpg" },
        { title: "2", url: "https://t1.picb.cc/uploads/2021/04/04/ZBnzty.jpg" },
        { title: "3", url: "https://t1.picb.cc/uploads/2021/04/04/ZBnLWX.jpg" },
      ],
    };
  },
  mounted() {
    this.video();
  },
  methods: {
    video() {
      const dp = new DPlayer({
        container: document.getElementById("dplayer"),
        autoplay: true,
        hotkey: true,
        preload: "auto",
        video: {
          url: "https://blinkfox.github.io/medias/videos/demo.mp4",
          pic: "https://t1.picb.cc/uploads/2021/04/04/ZBlUGj.jpg",
        },
      });
      // dp.seek(1);
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
  },
};
</script>

<style  lang="less" >
.home {
  .menu {
    height: 64px;
    position: relative;
    z-index: 9;
    color: white;
    font-weight: 900;
    .menu_cont {
      width: 100%;
      -webkit-box-shadow: none;
      box-shadow: none;
      position: fixed;
      .menu_cont_item {
        position: relative;
        height: 100%;
        max-width: 1125px;
        width: 90%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;

        .menu_cont_item_right {
          .el-menu {
            background-color: transparent !important;
            background-image: none;
            &.el-menu.el-menu--horizontal {
              border-bottom: none;
            }
            li {
              color: white;
              background: transparent !important;

              i {
                color: white !important;
              }
              &.is-active {
                background: transparent !important;
              }
              &.is-active:hover {
                background: #67636370 !important;
              }
              &:not(is-active):hover {
                background: #67636370 !important;
              }
            }
          }
        }
        .menu_cont_item_left {
          line-height: 64px;
          /deep/i {
            margin-right: 5px;
            width: 24px;
            text-align: center;
            font-size: 18px;
            vertical-align: middle;
            color: white !important;
          }
        }
      }
    }
  }

  .banner {
    height: 100%;
    width: 100%;
    margin-top: -64px;
    img {
      height: 100%;
      width: 100%;
    }
  }

  .right {
    float: right;
  }
  .left {
    float: left;
  }
  // 内容区
  .main {
    min-height: 772px;
    margin: 10px;
    .title {
      margin-bottom: 20px;
      font-size: 2rem;
      font-weight: 700;
    }
    .main_content {
      .dream {
        margin-top: 20px;
        margin-bottom: 40px;
        background: white;
      }
    }
  }
}
</style>



